import { getPopularAnime } from '@/lib/api'
import { Navbar } from '@/components/navbar'
import { AnimeGrid } from '@/components/anime-grid'
import { Suspense } from 'react'
import { SkeletonGrid } from '@/components/skeleton-loader'

async function PopularContent() {
  const data = await getPopularAnime(1)
  return <AnimeGrid animes={data.data} title="Popular Anime" />
}

export default function PopularPage() {
  return (
    <main className="min-h-screen bg-background">
      <Navbar />
      
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
        <h1 className="text-4xl font-bold mb-2 text-foreground">
          Popular Anime
        </h1>
        <p className="text-muted-foreground mb-8">
          Explore the most popular anime series right now
        </p>
      </div>

      <Suspense fallback={<SkeletonGrid />}>
        <PopularContent />
      </Suspense>
    </main>
  )
}
